<script>
  //验证码插件
  import {
    getCheckCode,
    getInfoSup,
    uploadCard,
    getUserInfoRegister,
    getMechanismInfo,
    getMenu,
    apiChangepwdbyPhone,
  } from '../../config/api'
  import api from '../../config/url.js'
  import {
    mapState,
    mapMutations
  } from 'vuex'
  import {
    dialogConfirm,
    isBroswer
  } from '../../config/mUtils'
  import {
    setStore,
    getStore,
    removeStore
  } from '../../config/mUtils'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        //-------------------------设置密码弹窗
        createDialogShow:false,
        newpwdErrorMsg:'',
        dialogParam:{
          newpwd:'',
          newpwd1:'',
        },

        step: 0,
        phoneNum: '',
        loginOk: false,
        loginOk3: false,
        //添加的参数
        companyName: '', //机构名称
        companySize: '', //机构规模
        trueName: '', //真实姓名
        position: '', //职位
  
        phone: '',
        invitecode_1: '',
        invitecode_2: '',
        invitecode_3: '',
        invitecode_4: '',
        invitecode_5: '',
        invitecode_6: '',
        showback:false,
        options: [{
          value: 1,
          label: '少于10人'
        }, {
          value: 2,
          label: '10～30人'
        }, {
          value: 3,
          label: '30～50人'
        }, {
          value: 4,
          label: '50～100人'
        }, {
          value: 5,
          label: '100人以上'
        }],
        getUserId: '',
        carUrl:'',
        cardFile:'',
        reason:'',
        type:null,
        key:'',
        focusStatus:1,
        domain_name:'',
        key:''
      }
    },
    components: {},
    created() {
      if (this.$route.query.phone == undefined) {
        this.$router.push({path: '/register'})
      }else{
        this.showback = true;
        this.phone = this.$route.query.phone 
        this.innitData();
      }
      setStore("htcx_webtoken", ''); //存储本次token
      setStore("htcx_uid", ''); //uid
      api.resetServer();
      window.localStorage.removeItem('htcx_server_address');
      window.localStorage.removeItem('htcx_server_key');
    },
    directives: {
        focus: {
            update: function (el, {value}) {
                if (value) {
                    el.focus();
                }
            }
        }
    },
    mounted() {},
    computed: {
       ...mapState([
        'userInfo', 'userUrl', 'userName'
      ]),
      haveCompanyName: function() {
        if (this.companyName == '') {
          return false
        } else {
          return true
        }
      },
      haveCompanySize: function() {
        if (this.companySize == '') {
          return false
        } else {
          return true
        }
      },
      haveTrueName: function() {
        if (this.trueName == '') {
          return false
        } else {
          return true
        }
      },
      havePosition: function() {
        if (this.position == '') {
          return false
        } else {
          return true
        }
      },
      loginOk1: function() {
        if (this.invitecode_1 != '' && this.invitecode_2 != '' && this.invitecode_3 != '' && this.invitecode_4 != '' && this.invitecode_5 != '' && this.invitecode_6 != '') {
          return true;
        } else {
          return false
        }
      },
      loginOk2: function() {
        if (this.haveCompanyName && this.haveCompanySize && this.haveTrueName && this.havePosition) {
          return true;
        } else {
          return false
        }
      },
      //弹窗验证---------------------------------
      newpwdError:function(){
        if(this.dialogParam.newpwd==''||this.dialogParam.newpwd.length<6||this.dialogParam.newpwd1==''||this.dialogParam.newpwd1.length<6){
          return true;
        }else{
          return false;
        }
      }
    },
    methods: {
      ...mapMutations([
        'RECORD_USERINFO', 'RECORD_USERURL','RECORD_USERNAME','SET_SERVER'
      ]),
       //弹窗-----------修改密码
      dialogSubmitClick(){
        if(this.dialogParam.newpwd==''||this.dialogParam.newpwd.length<6||this.dialogParam.newpwd1==''||this.dialogParam.newpwd1.length<6){
          this.newpwdErrorMsg='新密码不少于6位';
        }else{
          if(this.dialogParam.newpwd!=this.dialogParam.newpwd1){
            this.newpwdErrorMsg='两次密码不一致';
          }else{
            apiChangepwdbyPhone({
              phone:this.phone,
              password:this.dialogParam.newpwd
            }).then(res=>{
              if(res.data.msgNo==1){
                dialogConfirm('success','修改成功');
                this.$router.push({ path: '/home/homepage' })
              }else{
                dialogConfirm('fail',res.data.message.split('错误码')[0]);
              }
            })
          }
        }
      },
      innitData(){
        getUserInfoRegister({
          phone:this.phone
        }).then(res => {
          if (res.data.msgNo == 1) {
            // console.log(res);
            let data = res.data.payload;
             
            if(data ==null){
              this.step =1
            }else{
              this.getUserId = data.user_id;
              if(data.status==2){
                dialogConfirm('success', '审核成功,跳转到登录页面');
                this.$router.push({ path: '/login' })
                return false;
              }
              if(data.mechanism_name!=''&&data.business_card==''){
                this.toChangeInfo();
              }else{
                if(data.status==1){
                  this.step=5;
                }else if(data.status==0){
                  this.step=6;
                  this.reason= data.reason;
                }
              }
              
            }
          } else {
            let msg = res.data.message.split('错误码')[0];
            if (msg != undefined) {
              dialogConfirm('info', msg);
            }
          }
        })
      },
      //跳转导创建团队
      toCreatTeam() {
        this.step = 3;
        this.type = 2;
      },
      //验证邀请码是否存在
      toJoinTeam() {
        if (this.loginOk1) {
          let code = this.invitecode_1 + this.invitecode_2 + this.invitecode_3 + this.invitecode_4 + this.invitecode_5 + this.invitecode_6;
          // console.log(code);
          getCheckCode({
            phone: this.phone,
            code: code
          }).then(res => {
            if (res.data.msgNo == 1) {
              let data =res.data.payload;
              this.step =2;
              this.type = 1
              this.key = data.key;
              this.companyName = data.mechanism_name;
              this.companySize = data.scale;
              this.trueName = '';
              this.position = '';
              this.domain_name = data.domain_name;
              this.key = data.key;
              setStore("htcx_server_address", this.domain_name); //存储本次token;
              setStore("htcx_server_key", this.key); //存储本次token
              api.setServer(this.domain_name);
              // console.log(res);
            } else {
              let msg = res.data.message.split('错误码')[0];
              // console.log(res.data.message.split('错误码'));
              dialogConfirm('info', msg);
            }
          })
        }
      },
      //提交信息团队 个人信息 修改
      toCommitCard() {
        if (this.loginOk2) {
          let params = {
            phone: this.phone,
            position: this.position,
            name: this.trueName,
            type: this.type
          }
          if(this.type==1){
            params.key=this.key;
          }else if(this.type==2){
            params.scale=this.companySize;
            params.mechanism_name=this.companyName;
          }else if(this.type==3){
            params.key=this.key;
            params.scale=this.companySize;
            params.mechanism_name=this.companyName;
            params.user_id = this.getUserId;
          }
          getInfoSup(params).then(res => {
            if (res.data.msgNo == 1) {
              if(this.type==1){
                this.getMenuData(res.data.payload.user_id,this.phone);
              }else{
                this.getUserId = res.data.payload.user_id;
                this.step = 4
              }
              
            } else {
              let msg = res.data.message.split('错误码')[0];
              if (msg != undefined) {
                dialogConfirm('info', msg);
              }
            }
          })
        }
      }, 
      change(type){
        // console.log(type);
         
        this.focusStatus = this.focusStatus+1;
        // console.log(this.focusStatus);
        // if(type==1){
        //   if(this.invitecode_1==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }else if(type==2){
        //   if(this.invitecode_2==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }else if(type==3){
        //   if(this.invitecode_3==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }else if(type==4){
        //   if(this.invitecode_4==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }else if(type==5){
        //   if(this.invitecode_5==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }else if(type==6){
        //   if(this.invitecode_6==''){
        //     this.focusStatus = type;
        //   }else{
        //     this.focusStatus = this.focusStatus+1
        //   }
        // }
        
      },
      //提交名片
      toCommitPic() {
        if(this.loginOk3){
          uploadCard({
            user_id:this.getUserId
          },this.cardFile).then(res => {
            if (res.data.msgNo == 1) {
              // console.log(res);
              this.step = 5
            } else {
              let msg = res.data.message.split('错误码')[0];
              if (msg != undefined) {
                dialogConfirm('info', msg);
              }
            }
          })
        }
      },

      toChangeInfo(){
        getMechanismInfo({
            user_id:this.getUserId
          }).then(res => {
            if (res.data.msgNo == 1) {
              // console.log(res);
              let data = res.data.payload;
              this.companyName = data.mechanism_name;
              this.companySize = data.scale;
              this.trueName = data.name;
              this.position = data.position;
              // console.log(data.key);
              this.key = data.key;
              this.step = 3;
              this.type = 3;
            } else {
              let msg = res.data.message.split('错误码')[0];
              if (msg != undefined) {
                dialogConfirm('info', msg);
              }
            }
          })
      },


      //建立一個可存取到該file的url
      getObjectURL(file) {
        var url = null;
        if (window.createObjectURL != undefined) { // basic
          url = window.createObjectURL(file);
        } else if (window.URL != undefined) { // mozilla(firefox)
          url = window.URL.createObjectURL(file);
        } else if (window.webkitURL != undefined) { // webkit or chrome
          url = window.webkitURL.createObjectURL(file);
        }
        return url;
      },
      //修改图片内容
      changeToop(e) {
        let inputDOM = this.$refs.inputer;
        let file = inputDOM.files[0];
        if (file == undefined) {
            return false;
        }
        // console.log(file.type);
        if (!/.(gif|jpg|jpeg|png)$/.test(file.type)) {
            dialogConfirm('info','上传名片图片只能是jpg/png/gif格式')
            inputDOM.value='';
            return false
        }
        // console.log(this.getObjectURL(file));
        this.carUrl = this.getObjectURL(file);
        this.cardFile = file;
        inputDOM.value='';
        if(this.carUrl!=undefined){
          this.loginOk3 = true;
        }
      },

      //加入成功去提交
      getMenuData(id,phone){
        // console.log(api.url)
        getMenu({
          phone: phone,
          user_id:id,
        }).then(res => {
          if (res.data.msgNo == 1) {
            // console.log(res.data);
            setStore("htcx_webtoken", res.data.payload.webToken); //存储本次token
            setStore("htcx_autologin", false); //下次是否自动登录
            setStore("htcx_uid", res.data.payload.uid); //uid
            setStore("htcx_userInfo", res.data.payload); //uid
            setStore("htcx_userUrl", res.data.payload.url); //uid
            setStore("htcx_userName", res.data.payload.username); //uid
            this.RECORD_USERINFO(res.data.payload) //用户信息存全局
            this.RECORD_USERURL(res.data.payload.url); //用户信息存全局
            this.RECORD_USERNAME(res.data.payload.username); //用户信息存全局
            if(res.data.payload.have_password==1){
              this.createDialogShow=true;
              this.dialogParam={
                newpwd:'',
                newpwd1:''
              }  
            }else{
              this.$router.push({ path: '/home/homepage' })
            }  
          }else{
            dialogConfirm('fail',res.data.message.split('错误码')[0]);
            window.localStorage.removeItem('htcx_server_address');
            window.localStorage.removeItem('htcx_server_key');
            api.resetServer();
          }
        });
      },
      tabclick(name){
        // this.$router.push({
        //   name: name,
        // })
        window.location.href='http://www.pewinner.com';
        // window.location.href='/pe-introduce'
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../../style/modal.scss';
  @import '../../style/login1';
  /* 弹窗样式 */

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

.form-group-inputboxgroup {
  margin-bottom: 25px;
}

.dialog-btn-submit.btn-blue-border {
  margin-right: 20px;
}
</style>



